* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --blue: #00acdc;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav_list {
  text-transform: uppercase;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.nav_item {
  height: 100%;
  margin-right: 20px;
  text-decoration: none;
}

.nav_item:last-child {
  margin-right: 0;
}

.nav_link {
  color: #000;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.is-active {
  color: var(--blue);
  position: relative;
}

nav a:hover {
  color: var(--blue);
}

.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

